import React, {useState} from 'react'
import Rating from '@mui/material/Rating';
import CloudDownloadIcon from '@mui/icons-material/CloudDownload';
import CloudUploadIcon from '@mui/icons-material/CloudUpload';
import { experimentalStyled as styled } from '@mui/material/styles';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import PlayCircleIcon from '@mui/icons-material/PlayCircle';
import PauseCircleIcon from '@mui/icons-material/PauseCircle';
import StopCircleIcon from '@mui/icons-material/StopCircle';


export default function Mainmovie(props) {

    const Item = styled(Paper)(({ theme }) => ({
        backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
        ...theme.typography.body2,
        minWidth: '310px',
        marginTop: '2em',
        padding: theme.spacing(2),
        textAlign: 'center',
        color: theme.palette.text.secondary,
      }));
    
      const style = {
        item: {
            cursor: 'pointer', color: 'gold', fontSize: '3rem'
        }, 
        grid: {width: '300px'}
      }
    
      const [content, setshowcontent] = useState(false)
    
    
    
      const showMore = () => { 
        var video = document.getElementsByTagName('video')[0];
        setshowcontent(true)   
        console.log(video.duration)
      }
      
     const closeMoreInfo = () => {
        setshowcontent(false)
     }

     const playVideo = () => {
      document.querySelectorAll('.source')
      .forEach((el) => {
      el.play();
      console.log(el.duration/el.duration)
      });
     }

     
     const pauseVideo = () => {
      document.querySelectorAll('.source')
      .forEach((el) => {
      el.pause();
      });
      
     }

     const stopVideo = () => {
      document.querySelectorAll('.source')
      .forEach((el) => {
      el.currentTime=0;el.pause()
      });
     }

  return (
    <div>
         <Box sx={{ flexGrow: 2 }} >
            

            <Grid item xs={12} sm={6} md={4} lg={2} direction="row" style={style.grid} key={props.id}> 
                                
                                <Item key={props.id}>
                                    {
                                        content === false ? 
                                        (
<>
                                        <div className='main__cardText'><div className='main__cardText-nav'>Tytuł:<span> {props.title} <CloudDownloadIcon key={props.id} onClick={showMore} style={style.item}/></span></div></div>         
                                        <div className='main__cardText'><div className='main__cardText-nav'>Czas trwania:<span> {props.duration}</span></div></div>
                                        <div className='main__cardText'>
                                        <div className='main__cardText-nav'>Ocena:<span> <Rating name="read-only" value={props.value} readOnly precision={0.5} /> </span></div>
                                        </div>
                                        </>
                                        )
                                        
                                     :
                                     (
                                      <>
                                     <div className='main__cardText'><div className='main__cardText-nav'>Tytuł:<span> {props.title} <CloudUploadIcon key={props.id} onClick={closeMoreInfo} style={style.item}/></span></div></div>         
                                     <div className='main__cardText'><div className='main__cardText-nav'>Czas trwania:<span> {props.duration}</span></div></div>
                                     <div className='main__cardText'><div className='main__cardText-nav'>
                                        <video  autoPlay playsInline controls className='source'>
                                        <source 
                                        src={process.env.PUBLIC_URL + props.video_url} type="video/mp4"></source>
            
                                        </video>	
                                        </div></div>
                                        <PlayCircleIcon onClick={playVideo} style={style.item}/>
                                        <PauseCircleIcon onClick={pauseVideo} style={style.item} />
                                        <StopCircleIcon onClick={stopVideo} style={style.item}/>
                                     <div className='main__cardText'>
                                     <div className='main__cardText-nav'>Ocena:<span> <Rating name="read-only" value={props.value} readOnly precision={0.5} /> </span></div>
                                     </div>
                                     </>
                    
                                     )
                                     
                                    }
                                                          
                                </Item>
                         </Grid>
                     
                         </Box>                        
    </div>
  )
}
