import React, {useEffect} from 'react'
import './Welcome.scss'
import Aos from 'aos';

export default function Welcome() {

    useEffect (()=> {
        Aos.init({duration: 2000})
      }, [])


  return (
    <div>
        <div className="welcome">
            <div className="welcome__header" data-aos="fade-left">
                Lorem, ipsum dolor sit amet consectetur adipisicing elit. Repellendus eaque doloribus vitae officia, vero vel enim, quae alias suscipit incidunt animi nihil qui corrupti sint praesentium fugiat soluta odit saepe!
            </div> 
            <div className="welcome__img">
                    <img data-aos="fade-right" className="welcome__img" src={process.env.PUBLIC_URL + '/img/ticket640.jpg'} alt="zdjęcie do filmu" />
            </div>
        </div>
    </div>
  )
}
