import React, {useEffect} from 'react'
import 'aos/dist/aos.css'
import Aos from 'aos';
import './Movie.scss';
import Grid from '@mui/material/Grid';
import Mainmovie from '../Mainmovie/Mainmovie';


export default function Movie() {

    const movie = [
        {
            id: 0,
            title: 'Zmrok', 
            duration: 15,
            video_url: '/movie/movie1.mp4',
            value: 5,
           
           
        }, 
        
        {   
            id:1,
            title: 'Uśmiech', 
            duration: 32,
            video_url: '/movie/movie2.mp4', 
            value: 3.5,
           
            
        },
        {
            id: 2,
            title: 'Natural', 
            duration: 10,
            video_url: '/movie/movie3.mp4', 
            value: 4,
      
            
        }, 
        {
            id:3,
            title: 'Dyniowa', 
            duration: 18,
            video_url: '/movie/movie4.mp4',
            value: 4.5,
            
         
        }, 
        

    ]

    useEffect (()=> {
        Aos.init({duration: 2000})
      }, [])

     
 const movielist = movie.map((el) => <Mainmovie key={el.id} {...el}/>)

  return (
    <div>
        <main className='main'>
        <div data-aos="fade-right" className='main__popularText' >Najlepsze filmy. Sprawdź szczegóły
            <div className="main__popularText-underline"></div>
        </div>   
       
                     <Grid container spacing={2}
                     direction="row"
                    justifyContent="space-around"
                    alignItems="center"
                    rowSpacing={2}
                    >                    
                    
                    {movielist}
    
                   </Grid>
       
        </main>
       
    </div>
  )
}
